.App {
  display: inline-block;
  font-family: monospace;
  padding: 10px;
  position: relative;
  min-width: 300px;
  max-width: 560px;
  width: 100%;

  height: 100%;

  border: 2px solid #ccc;
  border-radius: 6px;
  overflow: hidden;

  .wrapper {
    width: 100%;
    display: inline-flex;
    flex-wrap: wrap;
    
    .calculator {
      max-width: 400px;
      width: 100%;
  
      input {
        min-width: 180px;
      }
    }
  
    .result {
      padding: 10px 0;
      text-indent: 4px;
      text-align: left;
  
      min-width: 120px;
  
      font-size: 14px;
      font-weight: 600;
      color: #555;

    }
  }

  

  .error__message {
    width: 300px;
    position: absolute;
    left: calc(50% - 150px);
    top: 10px;
    z-index: 2000;
  }
}
